/**Modern Waterfall**/
.themeModrnWatrFall .flatCard .post{
	-webkit-box-shadow: 0 1px 4px rgba(0,0,0,0.1);
    box-shadow: 0 1px 4px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 1px 4px rgba(0,0,0,0.1);
    background-clip: padding-box;
    border:0px;
    border-radius: 12px;
    overflow: hidden;
}

.themeModrnWatrFall .authorDetails img{ 
    position:static; float: none; clear:both; 
    margin:0px 0 0 0; width:50px; height:50px;
}
    .themeModrnWatrFall .authorDetails .author-avatar-block {
        height: 56px;
        width: 56px;
        border: 3px solid #ffffff;
        color: #ffffff;
      }
      
      .themeModrnWatrFall .authorDetails .author-avatar-block span {
      text-align: center;
      width: 100%;
      }
      
      .themeModrnWatrFall .flatCard h4 .author-avatar-block {
        float: none;
        clear: both;
        margin: 0px auto 7px auto;
        display: block;
        border-radius: 50%;
          -webkit-border-radius: 50%;
      }

     .themeModrnWatrFall .authrHandleTime .timePost {
        font: 600 10px/18px "open sans";
        text-decoration: none;
        display: inline-block;
        overflow: hidden;
        margin-right: 0;
    }
      

      .themeStart46 .authorDetails .author-avatar-block {
        border: 3px solid #ffffff;
        color: #ffffff;
        height: 48px;
        width: 48px;
        margin-top: -5px;
        margin-right: 8px;
        float: left;
      }
      
      .themeStart46 .authorDetails .author-avatar-block span {
      text-align: center;
      width: 100%;
      }
      
      .themeStart46 .flatCard h4 .author-avatar-block {
        clear: both;
        margin: 0px auto 7px auto;
        display: block;
        border-radius: 50%;
          -webkit-border-radius: 50%;
      }

.themeModrnWatrFall .authorDetails .authrHandle, 
.themeModrnWatrFall .authorDetails .timePost, 
.themeModrnWatrFall .sepratedot{
    float:none;
}
.themeModrnWatrFall .authorDetails .authrHandleTime{ line-height:100%;}
.themeModrnWatrFall .authorDetails .authrHandleTime *{vertical-align: middle; display:inline-block; margin:0; line-height: inherit; float:none; clear:both;}
.themeModrnWatrFall .authorDetails .authrHandleTime .sepratedot { margin: 0 5px !important; padding-top: 2px; }
.themeModrnWatrFall .flatCard h4 .postNetwork {  
    width: auto;
    height: auto;
    margin:8px auto;
    float:none;
}
.themeModrnWatrFall .flatCard h4 .postNetwork i{ font-size:30px !important;}
.themeModrnWatrFall .flatCard.onlyTextCard h4 .authorDetails{
    position: static;
    width: 100%;
}
.themeModrnWatrFall .flatCard.onlyTextCard h4 .postNetwork{
    position:static;
    transform:none;
    -moz-transform:none;
    -webkit-transform:none;
    margin-top: 10px;
}
.themeModrnWatrFall .flatCard.onlyTextCard .authorDetails img{margin-top:10px;}
.themeModrnWatrFall .flatCard.onlyTextCard .authorDetails .postAuthorName{margin-top:10px;}
.themeModrnWatrFall .flatCard.onlyTextCard .postContent .postedTxt{
    position:static;
    transform:none;
    -moz-transform:none;
    -webkit-transform:none;
}

.themeModrnWatrFall .authorDetails .postAuthorName {
    margin: 0 auto;
}


.themeModrnWatrFall.display #marqueecontainer{
height: 100vh;
scrollbar-width: none;
}    


.themeModrnWatrFall.display #wPosts{
margin-bottom: 400px;
}

.themeModrnWatrFall.display .tb_mwf_post_in{
    cursor: default;
}

.themeStart46 #marqueecontainer{
height: 100vh !important;
scrollbar-width: none;
}    

.themeStart46 #marqueecontainer::-webkit-scrollbar {
    width: 0px;
    background-color: #f5f5f5
}

.themeStart46  #wPosts{
    margin-bottom: 400px;
}

.WaterFallPixel .post{
    background-color: transparent !important;
}

.WaterFallPixel .theme65 .tb_fc_media_wrap .tb__icon{
    display: none;
}
.themeModrnWatrFall .theme65 .pixel65 .post{
    padding: 0px !important;
}

.pixel65 .tb_mwf_contant_wrapper{
    position: absolute;
    bottom: 0px;
    width: 100%;
    padding: 8px;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_author_wrapper{
    margin-bottom: 0px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_authorname{
    color: #fff !important;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_post_info > div{
    color: #fff !important;
    font-size: 11px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_post_info .tb_mwf_seprator{
    background-color: #fff !important;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_author_profile{
    height: 34px;
    width: 34px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_author_profile img{
    width: 100%;
    height: 100%;
}
